"use client";

import { useState, useEffect } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import { Bell, Check, Trash2, X, Filter, CheckCheck } from "lucide-react";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";

interface Notification {
  id: string;
  title: string;
  description: string;
  read?: boolean;
  timestamp?: string;
  type?: 'booking' | 'provider' | 'customer' | 'system';
}

export default function NotificationsSettingsPage() {
  const [notifications, setNotifications] = useState<Notification[]>([
    { 
      id: '1', 
      title: 'New booking confirmed', 
      description: 'Booking BK005 has been confirmed by the customer.',
      timestamp: '2 hours ago',
      type: 'booking'
    },
    { 
      id: '2', 
      title: 'Provider completed job', 
      description: 'Provider John Doe marked BK003 as completed.',
      read: true,
      timestamp: '5 hours ago',
      type: 'provider'
    },
    { 
      id: '3', 
      title: 'Cancellation request', 
      description: 'Customer Jane Smith requested to cancel BK004.',
      timestamp: '1 day ago',
      type: 'customer'
    },
    { 
      id: '4', 
      title: 'New provider registration', 
      description: 'A new provider has registered and is awaiting approval.',
      read: true,
      timestamp: '2 days ago',
      type: 'provider'
    },
    { 
      id: '5', 
      title: 'Payment received', 
      description: 'Payment of $250 received for booking BK005.',
      timestamp: '3 days ago',
      type: 'booking'
    },
    { 
      id: '6', 
      title: 'System maintenance scheduled', 
      description: 'System maintenance is scheduled for next Sunday at 2 AM.',
      read: true,
      timestamp: '1 week ago',
      type: 'system'
    },
  ]);

  const [filter, setFilter] = useState<'all' | 'unread' | 'read'>('all');
  const [typeFilter, setTypeFilter] = useState<'all' | 'booking' | 'provider' | 'customer' | 'system'>('all');

  const unreadCount = notifications.filter(n => !n.read).length;
  const readCount = notifications.filter(n => n.read).length;

  const filteredNotifications = notifications.filter(n => {
    const statusMatch = filter === 'all' || (filter === 'read' ? n.read : !n.read);
    const typeMatch = typeFilter === 'all' || n.type === typeFilter;
    return statusMatch && typeMatch;
  });

  const markAsRead = (id: string) => {
    setNotifications(prev => prev.map(n => 
      n.id === id ? { ...n, read: true } : n
    ));
  };

  const markAsUnread = (id: string) => {
    setNotifications(prev => prev.map(n => 
      n.id === id ? { ...n, read: false } : n
    ));
  };

  const markAllAsRead = () => {
    setNotifications(prev => prev.map(n => ({ ...n, read: true })));
  };

  const deleteNotification = (id: string) => {
    setNotifications(prev => prev.filter(n => n.id !== id));
  };

  const clearAllNotifications = () => {
    setNotifications([]);
  };

  const getTypeColor = (type?: string) => {
    switch (type) {
      case 'booking':
        return 'bg-green-100 text-green-700 border-green-200';
      case 'provider':
        return 'bg-amber-100 text-amber-700 border-amber-200';
      case 'customer':
        return 'bg-purple-100 text-purple-700 border-purple-200';
      case 'system':
        return 'bg-blue-100 text-blue-700 border-blue-200';
      default:
        return 'bg-gray-100 text-gray-700 border-gray-200';
    }
  };

  return (
    <div className="space-y-6">
      <Card>
        <CardHeader>
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-2">
              <Bell className="h-5 w-5 text-primary" />
              <CardTitle>Notifications</CardTitle>
            </div>
            <div className="flex items-center gap-2">
              <Badge variant="outline" className="bg-cyan-50 text-cyan-700 border-cyan-200">
                {unreadCount} Unread
              </Badge>
              <Badge variant="outline" className="bg-gray-50 text-gray-700 border-gray-200">
                {readCount} Read
              </Badge>
            </div>
          </div>
          <CardDescription>
            Manage and configure your notification preferences
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          {/* Actions Bar */}
          <div className="flex flex-wrap items-center justify-between gap-4 p-4 bg-muted/50 rounded-lg">
            <div className="flex items-center gap-2">
              <Filter className="h-4 w-4 text-muted-foreground" />
              <Select value={filter} onValueChange={(v: any) => setFilter(v)}>
                <SelectTrigger className="w-[140px]">
                  <SelectValue placeholder="Filter by status" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All</SelectItem>
                  <SelectItem value="unread">Unread</SelectItem>
                  <SelectItem value="read">Read</SelectItem>
                </SelectContent>
              </Select>
              <Select value={typeFilter} onValueChange={(v: any) => setTypeFilter(v)}>
                <SelectTrigger className="w-[140px]">
                  <SelectValue placeholder="Filter by type" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All Types</SelectItem>
                  <SelectItem value="booking">Booking</SelectItem>
                  <SelectItem value="provider">Provider</SelectItem>
                  <SelectItem value="customer">Customer</SelectItem>
                  <SelectItem value="system">System</SelectItem>
                </SelectContent>
              </Select>
            </div>
            <div className="flex items-center gap-2">
              <Button 
                variant="outline" 
                size="sm"
                onClick={markAllAsRead}
                disabled={unreadCount === 0}
              >
                <CheckCheck className="h-4 w-4 mr-2" />
                Mark All Read
              </Button>
              <Button 
                variant="outline" 
                size="sm"
                onClick={clearAllNotifications}
                disabled={notifications.length === 0}
                className="text-red-600 hover:text-red-700"
              >
                <Trash2 className="h-4 w-4 mr-2" />
                Clear All
              </Button>
            </div>
          </div>

          <Separator />

          {/* Notifications List */}
          <div className="space-y-3">
            {filteredNotifications.length === 0 ? (
              <div className="flex flex-col items-center justify-center py-12 text-muted-foreground">
                <Bell className="h-12 w-12 mb-4 opacity-20" />
                <p className="text-lg font-medium">No notifications</p>
                <p className="text-sm">You're all caught up!</p>
              </div>
            ) : (
              filteredNotifications.map((notification) => (
                <div
                  key={notification.id}
                  className={`flex items-start gap-4 p-4 rounded-lg border transition-all ${
                    notification.read 
                      ? 'bg-background border-border' 
                      : 'bg-cyan-50/50 border-cyan-200'
                  }`}
                >
                  {/* Status Indicator */}
                  <div className="mt-1">
                    <div 
                      className={`h-2.5 w-2.5 rounded-full ${
                        notification.read ? 'bg-muted-foreground/30' : 'bg-cyan-500'
                      }`} 
                    />
                  </div>

                  {/* Content */}
                  <div className="flex-1 space-y-1">
                    <div className="flex items-start justify-between gap-2">
                      <h4 className={`text-sm font-semibold ${
                        notification.read ? 'text-muted-foreground' : 'text-foreground'
                      }`}>
                        {notification.title}
                      </h4>
                      {notification.type && (
                        <Badge 
                          variant="outline" 
                          className={`text-xs ${getTypeColor(notification.type)}`}
                        >
                          {notification.type}
                        </Badge>
                      )}
                    </div>
                    <p className="text-sm text-muted-foreground">
                      {notification.description}
                    </p>
                    {notification.timestamp && (
                      <p className="text-xs text-muted-foreground">
                        {notification.timestamp}
                      </p>
                    )}
                  </div>

                  {/* Actions */}
                  <div className="flex items-center gap-1">
                    {notification.read ? (
                      <Button
                        variant="ghost"
                        size="icon"
                        onClick={() => markAsUnread(notification.id)}
                        className="h-8 w-8 text-muted-foreground hover:text-cyan-600"
                        title="Mark as unread"
                      >
                        <X className="h-4 w-4" />
                      </Button>
                    ) : (
                      <Button
                        variant="ghost"
                        size="icon"
                        onClick={() => markAsRead(notification.id)}
                        className="h-8 w-8 text-muted-foreground hover:text-green-600"
                        title="Mark as read"
                      >
                        <Check className="h-4 w-4" />
                      </Button>
                    )}
                    <Button
                      variant="ghost"
                      size="icon"
                      onClick={() => deleteNotification(notification.id)}
                      className="h-8 w-8 text-muted-foreground hover:text-red-600"
                      title="Delete"
                    >
                      <Trash2 className="h-4 w-4" />
                    </Button>
                  </div>
                </div>
              ))
            )}
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
